<template>
  <div class="m-add-image">
    <div class="m-add-image__container">
      <div class="m-add-image__container-tools">
        <div class="m-assets__image">
          <img
            :src="image.url"
            alt="" />
        </div>
      </div>
    </div>

    <div class="m-add-image__metadata">
      <strong>{{ image.width }}px ✕ {{ image.height }}px</strong>; Uploaded <span v-if="asset.user">by {{ asset.user ? asset.user.fullOrUserName : '' }}</span> on {{ formatDateAndTime(image.created_at) }}
    </div>

    <div class="m-add-image__edit">
      <div class="m-field is-active">
        <res-input
          count="500"
          block>
          <label>Alt text
            <div class="m-tooltip">
              <res-button
                id="alt-text-button"
                paddingless>
                <res-icon name="tooltip"/>
                <button/>
              </res-button>
              <res-dropdown
                placement="bottom-start"
                for="alt-text-button"
                design="dark">
                Describe the important elements in the image for people who use assistive technology.
                <a
                  href="https://chorussupport.zendesk.com/hc/en-us/articles/360026524334"
                  target="_blank"
                  rel="noopener noreferrer">Learn more</a>
              </res-dropdown>
            </div>
          </label>
          <textarea v-model="body.title" />

        </res-input>
      </div>
      <div class="m-field is-active">
        <res-input block>
          <label>Credit</label>
          <textarea
            v-model="body.credit"
            rows="1" />
        </res-input>
      </div>
      <div class="m-field is-active">
        <res-input block>
          <label>Tags</label>
          <textarea v-model="body.search_text" rows="1" />
        </res-input>
      </div>
      <div class="m-field is-active">
        <res-input block>
          <label>Background
            <div class="m-tooltip">
              <res-button
                id="edit-background-button"
                paddingless>
                <res-icon name="tooltip"/>
                <button/>
              </res-button>
              <res-dropdown
                placement="bottom-start"
                for="edit-background-button"
                design="dark">
                Use this for context about the image. This text will not be visible to audiences.
              </res-dropdown>
            </div>
          </label>
          <textarea v-model="body.source_caption" />
        </res-input>
      </div>
      <div class="m-field is-active">
        <res-input block>
          <label>Image rights</label>
          <input
            v-model="body.usage_rights"
            type="text"/>
        </res-input>
      </div>
      <res-button
        design="primary"
        class="m-assets__drawer--submit-button"
        @click="updateAsset()">
        <button>Update image</button>
      </res-button>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex';
import { utilities } from '../../mixins/utilities.js';

export default {
  name: 'AssetEdit',
  mixins: [utilities],
  data() {
    return {
      body: {}
    };
  },
  computed: {
    ...mapState({
      asset: state => state.editAsset,
      image: state => state.editImage,
    })
  },
  mounted() {
    this.body = Object.assign({}, this.image);
  },
  methods: {
    async updateAsset() {
      await this.$store.dispatch('updateAsset', this.body);
    }
  }
};
</script>
