<template>
  <div>
    <div v-if="!showMetadata">
      <div class="m-assets__drawer-title">Upload</div>
      <input ref="fileSelect"
             type="file"
             multiple
             class="m-assets__drawer-file-upload"
             @change="previewFiles"/>
      <res-button design="primary" @click.prevent="$refs.fileSelect.click()">
        <res-icon name="desktop"/>
        <button>From your device</button>
      </res-button>
      <div class="m-assets__drawer-helper-text">For batch upload you may select multiple files at a time.</div>
      <div class="m-assets__drawer-option">or</div>
      <res-input block>
        <label for="remote-url">Remote file URL</label>
        <input id="remote-url" v-model="remoteFileUrl" type="text" @keyup.enter="setShowMetadata(true)"/>
      </res-input>
    </div>

    <div id="preview" class="p-flex p-flex--justify-between p-flex--wrap"/>

    <div v-if="showMetadata">
      <div class="m-add-image__edit">
        <div class="m-field is-active">
          <res-input
            count="500"
            block>
            <label for="alt-text">Alt text
              <div class="m-tooltip">
                <res-button
                  id="alt-text-button"
                  paddingless>
                  <res-icon name="tooltip"/>
                  <button/>
                </res-button>
                <res-dropdown
                  placement="bottom-start"
                  for="alt-text-button"
                  design="dark">
                  Describe the important elements in the image for people who use assistive technology.
                  <a
                    href="https://chorussupport.zendesk.com/hc/en-us/articles/360026524334"
                    target="_blank"
                    rel="noopener noreferrer">Learn more</a>
                </res-dropdown>
              </div>
            </label>
            <textarea id="alt-text" v-model="body.title" />

          </res-input>
        </div>
        <div class="m-field is-active">
          <res-input block>
            <label for="credit">Credit</label>
            <textarea
              id="credit"
              v-model="body.credit"
              rows="1" />
          </res-input>
        </div>
        <div class="m-field is-active">
          <res-input block>
            <label for="search_text">Tags</label>
            <textarea id="search_text" v-model="body.search_text" rows="1" />
          </res-input>
        </div>
        <div class="m-field is-active">
          <res-input block>
            <label for="source_caption">Background
              <div class="m-tooltip">
                <res-button
                  id="edit-background-button"
                  paddingless>
                  <res-icon name="tooltip"/>
                  <button/>
                </res-button>
                <res-dropdown
                  placement="bottom-start"
                  for="edit-background-button"
                  design="dark">
                  Use this for context about the image. This text will not be visible to audiences.
                </res-dropdown>
              </div>
            </label>
            <textarea id="source_caption" v-model="body.source_caption" />
          </res-input>
        </div>
        <div class="m-field is-active">
          <res-input block>
            <label for="usage_rights">Image rights</label>
            <input
              id="usage_rights"
              v-model="body.usage_rights"
              type="text"/>
          </res-input>
        </div>
        <res-button
          design="primary"
          class="m-assets__drawer--submit-button"
          @click="uploadAsset()">
          <res-icon name="upload"/>
          <button>Upload</button>
        </res-button>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'AssetUpload',
  data() {
    return {
      acceptFileTypes: '.gif,.jpeg,.jpg,.png',
      files: [],
      body: {
        title: '',
        credit: '',
        search_text: '',
        source_caption: '',
        usage_rights: ''
      },
      remoteFileUrl: null,
    };
  },
  computed: {
    showMetadata() {
      return this.$store.state.show_metadata;
    }
  },
  watch: {
    showMetadata(val) {
      if (!val) {
        this.files = [];
        this.body = {
          title: '',
          credit: '',
          search_text: '',
          source_caption: '',
          usage_rights: ''
        };
        this.remoteFileUrl = null;
        this.setShowMetadata(false);
        document.querySelector('#preview').innerHTML = '';
      }
    }
  },
  methods: {
    previewFiles(e) {
      this.setShowMetadata(true);
      const preview = document.querySelector('#preview');
      this.files = Array.from(e.target.files || e.dataTransfer.files);

      async function readAndPreview(file) {
        // Make sure `file.name` matches our extensions criteria
        if ( /\.(jpe?g|png|gif|webp)$/i.test(file.name) ) {
          let reader = new FileReader();
          let div = document.createElement('div');
          div.className = 'm-assets__drawer-image';
          reader.addEventListener('load', function () {
            div.style.backgroundImage = `url(${this.result})`;
            preview.appendChild( div );
          }, false);

          reader.readAsDataURL(file);
        }
      }

      if (this.files) {
        [].forEach.call(this.files, readAndPreview);
      }
    },
    uploadAsset() {
      this.$store.commit('SET_SIDEBAR', null);
      this.$store.commit('appLoader/SET_LOADING', true);
      this.$store.dispatch('uploadAssets', { files: this.files, body: this.body, remoteFileUrl: this.remoteFileUrl })
        .then(res => res.json())
        .then(data => {
          this.$store.commit('appLoader/SET_LOADING', false);
          if (data.message) {
            let msg = data.message.endsWith('.') ? `${data.message}` : `${data.message}.`;
            this.displayMsg('negative', msg );
          } else if (Array.isArray(data)) {
            let msg = data.length === 1 ? `1 asset has been uploaded.` : `${data.length} assets have been uploaded.`;
            data = data.map(item => {
              return this.formatData(item);
            });
            this.$store.dispatch('addAssets', data);
            this.displayMsg('positive', msg, true, 'check');
          } else { // if object then this is file uploaded from a url
            data = this.formatData(data);
            this.$store.dispatch('addAssets', [data]);
            this.displayMsg('positive', `Asset uploaded from a url.`, true, 'check');
          }
          this.resetData();
        })
        .catch(err => {
          this.$store.commit('appLoader/SET_LOADING', false);
          let errMsg = err.message.endsWith('.') ? err.message : `${err.message}.`;
          this.displayMsg('negative', `The upload failed. ${errMsg}`);
          this.resetData();
        });
    },
    setShowMetadata(val) {
      this.$store.commit('SET_SHOW_METADATA', val);
    },
    resetData() {
      this.files = [];
      this.body = {
        title: '',
        credit: '',
        search_text: '',
        source_caption: '',
        usage_rights: ''
      };
      this.remoteFileUrl = null;
    },
    displayMsg(type, msg, timeout=false, icon=null) {
      this.$store.commit('messageBanner/PUT_NOTICE', {
        type: type,
        message: msg,
        timeout: timeout,
        icon: icon
      });
    },
    formatData(data) {
      data.fileName = data.file_name;
      data.thumbnailUrl = data.thumbnail_url;
      data.type = data.content_type_category;
      data.searchText = data.search_text;
      return data;
    }
  }
};
</script>

