<template>
  <div class="p-flex--v m-nav--assets">
    <message />
    <nav class="m-nav m-nav--dashboard">
      <community-select
        v-if="community && currentUser"
        v-model="communityId"
        :community="community"
        :user="currentUser"
        :activeMemberships="activeMemberships"
        :allowedRoles="allowedRoles"
        class="m-community-select"
      />
    </nav>
    <div class="l-content p-flex is-compose">
      <app-drawer
        :class="currentSidebar ? 'm-assets__drawer-open' : ''"
        class="m-assets__drawer-slideIn"/>
      <div class="l-content__inner p-flex--v">
        <dashboard
          v-if="community && entitlements"
          :community="community"
          :user="currentUser"
          :entitlements="entitlements"
        />
      </div>
    </div>
    <confirm-delete-asset
      v-if="community && community.network"
      :networkName="community.network.name"
    />
    <app-loader />
  </div>
</template>

<script>
import * as cmd from './store/commands';
import request from '../client/lib/request';
import { mapState, mapGetters } from 'vuex';
import CommunitySelect from '../shared/vue/community-select/index.vue';
import Dashboard from './components/dashboard.vue';
import ConfirmDeleteAsset from './components/confirm-delete-asset.vue';
import AppLoader from '../shared/vue/app-loader/app-loader.vue';
import AppDrawer from './components/app-sidebar.vue';
import Message from '../shared/vue/banner-messages/message.vue';
import { CurrentUser } from '../shared/vue/queries/currentUser.graphql';
import { CurrentCommunity } from '../shared/vue/queries/currentCommunity.graphql';

export default {
  components: {
    CommunitySelect,
    Dashboard,
    ConfirmDeleteAsset,
    Message,
    AppLoader,
    AppDrawer
  },
  data() {
    return {
      entitlements: []
    };
  },
  computed: {
    ...mapState(['currentSidebar']),
    ...mapGetters(['activeMemberships']),
    communityId() {
      if (this.$route.query.community_id) {
        return parseInt(this.$route.query.community_id);
      } else {
        return this.getExternalCommunityId();
      }
    },
    allowedRoles() {
      return this.$root.config.policyRoles.app.access;
    }
  },
  created() {
    document.title = `Assets | ${this.$root.config.appName}`;
  },
  apollo: {
    currentUser: {
      query: CurrentUser,
      result({ data }) {
        this.$store.commit(cmd.SET_CONTEXT, { user: data.currentUser });
      }
    },
    community: {
      query() {
        return CurrentCommunity;
      },
      result({ data }) {
        this.$store.commit(cmd.SET_CONTEXT, { community: data.community });
        this.getEntitlements();
      },
      skip() {
        return !this.communityId;
      },
      variables() {
        return { id: this.communityId };
      }
    }
  },
  methods: {
    async getEntitlements() {
      this.entitlements = await request(`/api/entitlements?communityId=${this.communityId}`);
      this.$store.commit(cmd.SET_CONTEXT, { entitlements: this.entitlements });
    },
  }
};
</script>
