<template>
  <div
    :class="[
      { 'is-leaving': deleteCompleted && (assetToDelete.id === asset.id) },
      { 'm-assets__assets-edited': (editingAsset.id === asset.id) },
      { 'm-assets__assets-edited': asset.hasOwnProperty('search_text') && showHighlight }
    ]"
    class="m-assets__cards__card">
    <a
      :href="asset.url"
      rel="external"
      target="_blank"
    >
      <div
        v-if="asset.type.toLowerCase() === 'image'"
        :style="setBackgroundImage()"
        class="m-assets__cards__card-image"
      />
      <div v-else-if="asset.type.toLowerCase() === 'file'">
        <div
          class="m-assets__cards__card-image p-flex p-flex--justify-center p-flex--align-center"
        >
          <res-icon
            name="text-block"
            size="large"/>
        </div>
      </div>
      <div v-else-if="asset.type.toLowerCase() === 'audio'">
        <div
          class="m-assets__cards__card-image p-flex p-flex--justify-center p-flex--align-center"
        >
          <res-icon
            name="volume-high"
            size="large"/>
        </div>
      </div>
    </a>
    <div class="m-assets__cards__card-info">
      <div class="m-assets__cards__card-label">Date modified</div>
      <div class="m-assets__cards__card-text">{{ formatDate(asset.updatedAt) }}</div>
      <div class="m-assets__cards__card-label">Filename</div>
      <div class="m-assets__cards__card-text m-assets__cards__card-filename" role="button" @click.prevent="editAsset($event, asset.type)">{{ assetName }}</div>
      <div class="m-assets__cards__card-label">Credit</div>
      <div class="m-assets__cards__card-text">{{ asset.credit }}</div>
      <div class="m-assets__cards__card-label">Tags</div>
      <div class="m-assets__cards__card-text">{{ asset.searchText }}</div>
      <div
        v-if="canDeleteAssets"
        class="m-assets__cards__card-action"
      >
        <div v-if="deleteInProgress && !deleteCompleted && assetToDelete.id == asset.id">
          Deleting
        </div>
        <div v-else>
          <div v-if="canDeleteAsset">
            <button
              :id="'button-' + index"
              :disabled="deleteCompleted && assetToDelete.id === asset.id"
              class="m-assets__pointer"
            >
              <res-icon
                size="medium"
                title="Menu"
                name="more"
                animated="false"/>
            </button>

            <res-dropdown
              :for="'button-' + index"
              placement="left">
              <button v-if="asset.type.toLowerCase() === 'image'" @click.prevent="editAsset($event, asset.type)">
                <res-icon
                  name="edit"
                  size="small"/>
                Edit
              </button>
              <button
                type="button"
                @click.prevent="copyImageUrl($event, asset.url)">
                <res-icon
                  name="copy"
                  size="small"/>
                Copy URL
              </button>

              <button
                res-modal-open="confirmDeleteAssetResModal"
                @click="prepDelete()">
                <res-icon
                  name="trash"
                  size="small" />
                Delete
              </button>
            </res-dropdown>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { utilities } from '../mixins/utilities.js';
import { mapState } from 'vuex';

export default {
  name: 'Card',
  mixins: [utilities],
  props: {
    asset: {
      type: Object,
      required: true
    },
    currentUserId: {
      type: Number,
      required: true
    },
    canDeleteAssets: {
      type: Boolean,
      required: true,
      default: false
    },
    hasImagePlaceholder: {
      type: Boolean,
      required: true,
      default: false
    },
    index: {
      type: Number,
      required: true
    },
  },
  computed: {
    ...mapState(['showHighlight'])
  }
};
</script>
