import moment from 'moment-timezone';
import { mapState } from 'vuex';

export const utilities = {
  computed: {
    assetName() {
      return this.asset.fileName || this.asset.title || 'Untitled asset';
    },
    canDeleteAsset() {
      // images can only be deleted if canDeleteAssets is true and a replacement image url exists for the network
      if (this.type === 'IMAGE') {
        return this.canDeleteAssets && this.hasImagePlaceholder;
      }
      return this.canDeleteAssets;
    },
    ...mapState({
      deletePending: state => state.deletePending,
      deleteInProgress: state => state.deleteInProgress,
      deleteCompleted: state => state.deleteCompleted,
      assetToDelete: state => state.assetToDelete,
      editingAsset: state => state.editAsset
    })
  },
  methods: {
    formatDate(date) {
      return moment(date).format('MMM D, YYYY');
    },
    formatTime(time) {
      return moment(time).format('hh:mm A z');
    },
    formatDateAndTime(dateTime) {
      return moment(dateTime).format('MMM D, YYYY [at] hh:mm A z');
    },
    setBackgroundImage() {
      return { backgroundImage: `url(${this.asset.thumbnailUrl})` };
    },
    copyImageUrl(evt, url) {
      this.closeDropdown(evt);
      this.copyToClipboard(url);
    },
    async copyToClipboard(str) {
      await navigator.clipboard.writeText(str);
    },
    closeDropdown(evt) {
      let el = evt.target;
      while (el && el.tagName) {
        if (/res-dropdown/i.test(el.tagName)) return el.toggle();
        el = el.parentNode;
      }
    },
    prepDelete() {
      this.$store.commit('SET_DELETE_PENDING', true);
      this.$store.commit('SET_ASSET_TO_DELETE', this.asset);
    },
    editAsset(evt, type) {
      this.closeDropdown(evt);
      if (type.toLowerCase() === 'image') {
        this.$store.commit('SET_SIDEBAR', null);
        this.$store.dispatch('handleEditing', this.index).then(() => {
          this.$store.commit('SET_SIDEBAR', { to: 'asset-edit', title: 'Edit asset' });
        });
      }
    },
  }
};
