<template>
  <main class="m-assets">
    <header class="m-assets__header">

      <div class="m-assets__header-search">
        <res-input block>
          <label>Search</label>
          <input
            v-model.lazy="query"
            type="search"
            name="q"
            placeholder="Title, Tags, Type, Background, Updated by..."
            @input="clearSearchQuery($event.target.value)"
          />
          <res-icon
            slot="inner-right"
            name="search"
          />
        </res-input>
      </div>

      <div class="m-assets__header__buttons">
        <res-switch class="m-assets__header-uploads">
          <input
            v-model="ownUploads"
            type="checkbox"
            @change="showMyUploads"
          />
          <label>Only show my uploads</label>
        </res-switch>
        <res-select class="m-assets__header-select">
          <label>Filter by:</label>
          <select
            :value="category"
            name="category"
            @change="onCategoryChange($event.target.value)"
          >
            <option
              v-for="categoryOpt in availableCategories"
              :key="categoryOpt.value"
              :value="categoryOpt.value"
            >
              {{ categoryOpt.title }}
            </option>
          </select>
        </res-select>
        <res-toggle-buttons class="m-assets__header-toggle-button">
          <res-button
            :active="displayGrid"
            value="1"
            @click="toggleGrid(true)">
          <res-icon name="list"/><button/></res-button>
          <res-button
            :active="!displayGrid"
            value="2"
            @click="toggleGrid(false)">
          <res-icon name="grid"/><button/></res-button>
        </res-toggle-buttons>

      </div>
    </header>

    <div class="m-assets__sub-head">
      <div class="m-dashboard__sub-head__title">
        <h1>Assets</h1>
        <res-button
          type="default"
          paddingless
          class="m-l-1">
          <res-icon
            size="small"
            name="back"/>
          <a
            :href="`/?community_id=${currentCommunity.id}`"
            rel="external"
          >
            Back to dashboard
          </a>
        </res-button>
      </div>
      <res-button design="primary" @click.prevent="uploadAsset()">
        <res-icon name="upload"/>
        <button>Upload asset</button>
      </res-button>
    </div>

    <div
      v-if="displayResultsInfo"
      class="m-dashboard__sub-head__summary"
    >
      <span v-if="assets.length === 0">
        <span class="m-dashboard__sub-head--bold">No results</span> for
      </span>
      <span v-else>
        <span class="m-dashboard__sub-head--bold">{{ pagination.totalCount }}</span> {{ pagination.totalCount > 1 ? 'results' : 'result' }} for
      </span>
      {{ query }}
    </div>

    <component
      v-if="currentUserId && currentComponent"
      :is="currentComponent"
      :assets="assets"
      :isMobile="isMobile"
      :currentUserId="currentUserId"
      :canDeleteAssets="canDeleteAssets"
      :hasImagePlaceholder="currentCommunity.hasImagePlaceholder"/>

    <footer class="m-dashboard__footer">
      <div
        v-if="pagination"
        class="m-dashboard__footer__pagination m-assets__footer__pagination"
      >
        <res-pagination
          id="assets-pagination"
          :count="pagination.totalCount"
          :display="perPage"
          :ticks="ticks"
          :page="page"
          indicator="page"
          navigation="text"
          @click="paginate()"
        />
      </div>
    </footer>
  </main>
</template>

<script>
import querystring from 'querystring';
import { isEqual } from 'lodash';
import { mapState } from 'vuex';
import Assets from './assets.vue';
import Cards from './cards.vue';
import messages from '../../client/config/messages';
import { AssetsByCommunityId, AssetsByNetworkId } from '../queries/assets.graphql';

const MOBILE_WIDTH = 800;
const TYPE_KEYWORD_PATTERN = /type:(image|file|audio)\s?/;
export default {
  components: {
    Assets,
    Cards
  },
  data() {
    return {
      isMobile: false,
      displayGrid: true,
      displayResultsInfo: false,
      category: '',
      query: '',
      page: 1,
      perPage: 36,
      ticks: 7,
      pagination: null,
      communityId: null,
      availableCategories: [
        { value: '', title: 'All types' },
        { value: 'image', title: 'Images' },
        { value: 'file', title: 'Files' }
      ],
      ownUploads: false
    };
  },
  computed: {
    ...mapState([
      'isEditing',
      'editAsset',
      'editImage',
      'assets',
      'currentUser',
      'currentCommunity',
      'entitlements'
    ]),
    currentUserId() {
      return this.currentUser.id;
    },
    canDeleteAssets() {
      return this.currentUser.networkRoles.includes('NETWORK_ADMIN') || this.currentUser.memberships.some(m => {
        return m.community.id === this.currentCommunity.id && m.status === 'ACTIVE' && m.roles.includes('EDITOR');
      });
    },
    useNetworkAssets() {
      return (this.entitlements || []).includes('use_network_asset_search');
    },
    currentComponent() {
      return this.displayGrid ? 'assets' : 'cards';
    }
  },
  watch: {
    // As the query text changes, update the selected category if a matching
    // type keyword query is present
    query(current) {
      let [match, typeCategory] = current.match(TYPE_KEYWORD_PATTERN) || [];
      if (match && this.category !== typeCategory) {
        this.category = typeCategory;
      } else if (!match && this.category) {
        this.category = '';
      }
    },
    '$route.query'(current, prev) {
      if (parseInt(current.community_id, 10) !== parseInt(prev.community_id, 10)) {
        this.$emit('communityId', Number(current.community_id));
        this.page = 1;
        this.query = '';
      } else if (current.q !== prev.q || current.category !== prev.category) {
        // when the query or category changes, reset the page param
        this.page = 1;
      }
    }
  },
  beforeMount() {
    this.setInitialData();
    // set and track state of window width
    this.isMobile = window.innerWidth <= MOBILE_WIDTH;
    window.addEventListener('resize', () => this.isMobile = window.innerWidth <= MOBILE_WIDTH);
    if (localStorage.getItem('displayGrid')) {
      this.displayGrid = JSON.parse(localStorage.getItem('displayGrid'));
    }
  },
  apollo: {
    assets: {
      query() {
        return (this.useNetworkAssets ? AssetsByNetworkId : AssetsByCommunityId);
      },
      skip() {
        if (this.currentCommunity && this.currentCommunity.network) {
          return false;
        }
        return true;
      },
      variables() {
        return {
          communityId: this.currentCommunity.id,
          query: this.query,
          pageNumber: this.page,
          perPage: this.perPage,
          deleted: false,
          networkId: this.currentCommunity.network.id,
          sources: this.query.includes('source_id:') ? ['GETTY'] : ['URL', 'UPLOAD']
        };
      },
      fetchPolicy: 'cache-and-network',
      manual: true,
      result({ data, loading, error }) {
        if (!loading) {
          if (error) {
            if (error.gqlError.message === 'access denied') {
              this.$store.commit('messageBanner/PUT_NOTICE', {
                type: 'negative',
                message: messages.NETWORK_ASSETS_ACCESS_DENIED
              });
            } else {
              this.$store.commit('messageBanner/PUT_NOTICE', {
                type: 'negative',
                message: messages.UNEXPECTED
              });
            }
          } else {
            let response = data.chorusAssetsPage;

            let assets = response.results.map(asset => {
              return {
                ...asset,
                // map richtext into flat fields
                caption: asset.caption ? asset.caption.html : null,
                credit: asset.credit ? asset.credit.html : null,
              };
            });
            this.$store.commit('SET_ASSETS', assets);
            this.pagination ={
              pageNumber: response.pageNumber,
              totalCount: response.totalCount
            };
            this.displayResultsInfo = this.query.length > 0;
            // Update the query for the new params
            this.updatePath();
          }
        }
      }
    }
  },
  created() {
    document.title = `Assets | ${this.$root.config.appName}`;
  },
  methods: {
    async setInitialData() {
      // set data from the querystring if it exists
      let queryParams = this.$route.query;
      this.page = parseInt(queryParams.page, 10) || 1;
      this.query = queryParams.q || '';
    },
    clearSearchQuery(input) {
      // search again when the field is cleared
      if (input === '' && this.query !== input) {
        this.query = '';
      }
    },
    paginate() {
      this.page = document.getElementById('assets-pagination').page;
    },
    updatePath() {
      let params = {
        q: this.query,
        page: this.page.toString(),
        community_id: this.currentCommunity.id.toString()
      };
      // on load, don't reroute us
      if (!isEqual(params, this.$route.query)) {
        // Apollo uses reactive parameters to re-execute searches based on
        // changes to the corresponding Vue properties. However, we need to
        // set keep the query string in the url up-to-date with the actual query.
        let path = `/assets?${querystring.stringify(params)}`;
        this.$router.push({ path });
      }
    },
    onCategoryChange(category) {
      let [match, typeCategory] = this.query.match(TYPE_KEYWORD_PATTERN) || [];

      // When the user changes the category option, update the query text to include
      // the matching type keyword query:
      //
      // A) There is no selected category and a type keyword query exists in the
      //    search input: remove the type keyword query
      // B) There is a selected category and there is either no type keyword query
      //    in the search input or there is a different type keyword query: update
      //    the type keyword query text to match
      if (!category && match) {
        this.query = this.query.replace(TYPE_KEYWORD_PATTERN, '').trim();
      } else if (category && (!match || typeCategory !== category)) {
        let query = this.query.replace(TYPE_KEYWORD_PATTERN, '').trim();
        this.query = `type:${category} ${query}`;
      }
    },
    toggleGrid(val) {
      this.displayGrid = val;
      localStorage.setItem('displayGrid', JSON.stringify(val));
    },
    showMyUploads() {
      if (this.ownUploads) {
        this.query = `by:me ${this.query}`.trim();
        this.ownUploads = this.query;
      } else {
        this.query = this.query.replace(/by:me/, '').trim();
        this.ownUploads = this.query;
      }
    },
    uploadAsset() {
      this.$store.commit('SET_SIDEBAR', null);
      this.$store.commit('SET_SHOW_METADATA', false);
      this.$store.commit('SET_SIDEBAR', { to: 'asset-upload', title: 'New asset' });
    }
  }
};
</script>
