<template>
  <tr :class="[
    'm-dashboard__table__row',
    { 'is-leaving': deleteCompleted && (assetToDelete.id === asset.id) },
    { 'm-assets__assets-edited': (editingAsset.id === asset.id) },
    { 'm-assets__assets-edited': asset.hasOwnProperty('search_text') && showHighlight }
  ]">
    <td class="m-assets__table__image">
      <a
        :href="asset.url"
        rel="external"
        target="_blank"
      >
        <div
          v-if="asset.type.toLowerCase() === 'image'"
          :style="setBackgroundImage()"
          class="m-assets__table__table-image"
        />
        <div
          v-else-if="asset.type.toLowerCase() === 'file'"
          class="m-assets__table__table-file"
        >
          <res-icon
            name="text-block"
            size="large"/>
        </div>
        <div
          v-else-if="asset.type.toLowerCase() === 'audio'"
          class="m-assets__table__table-file"
        >
          <res-icon
            name="volume-high"
            size="large"/>
        </div>
      </a>
    </td>
    <td
      v-if="isMobile"
      class="m-assets__table__updated-at"
      data-heading="Date modified"
    >
      {{ formatDateAndTime(asset.updatedAt) }}
    </td>
    <td v-else>{{ formatDate(asset.updatedAt) }}<br/>{{ formatTime(asset.updatedAt) }}</td>
    <td
      role="button"
      class="m-assets__table__filename"
      data-heading="Filename"
      @click.prevent="editAsset($event, asset.type)"
    >
      {{ assetName }}
    </td>
    <td
      class="m-assets__table__credit"
      data-heading="Credit">
      {{ asset.credit }}
    </td>
    <td
      class="m-assets__table__tags"
      data-heading="Tags"
    >
      <div>
        {{ asset.searchText }}
      </div>
    </td>
    <td class="m-assets__table__copy-url">
      <res-button @click.prevent="copyImageUrl($event, asset.url)">
        <res-icon
          name="copy"
          size="large"
        />
        <button/>
      </res-button>

    </td>
    <td
      v-if="canDeleteAssets"
      class="m-assets__table__action"
    >
      <div v-if="deleteInProgress && !deleteCompleted && assetToDelete.id == asset.id">
        Deleting
      </div>

      <div v-else>
        <div v-if="canDeleteAsset ">
          <button
            :id="'button-' + index"
            :disabled="deleteCompleted && assetToDelete.id === asset.id"
            class="m-assets__pointer"
          >
            <res-icon
              size="medium"
              title="Menu"
              name="more"
              animated="false"/>
          </button>
          <res-dropdown
            :for="'button-' + index"
            placement="left"
            style="z-index: 2;">
            <button v-if="asset.type.toLowerCase() === 'image'" @click.prevent="editAsset($event, asset.type)">
              <res-icon
                name="edit"
                size="small"/>
              Edit
            </button>

            <button
              type="button"
              @click.prevent="copyImageUrl($event, asset.url)">
              <res-icon
                name="copy"
                size="small"/>
              Copy URL
            </button>

            <button
              res-modal-open="confirmDeleteAssetResModal"
              @click="prepDelete()">
              <res-icon
                name="trash"
                size="small" />
              Delete
            </button>
          </res-dropdown>
        </div>

      </div>

    </td>
  </tr>
</template>

<script>
import { utilities } from '../mixins/utilities.js';
import { mapState } from 'vuex';

export default {
  name: 'Asset',
  mixins: [utilities],
  props: {
    asset: {
      type: Object,
      required: true
    },
    isMobile: {
      type: Boolean,
      default: false
    },
    currentUserId: {
      type: Number,
      required: true
    },
    canDeleteAssets: {
      type: Boolean,
      required: true,
      default: false
    },
    hasImagePlaceholder: {
      type: Boolean,
      required: true,
      default: false
    },
    index: {
      type: Number,
      required: true
    }
  },
  computed: {
    ...mapState(['showHighlight'])
  }
};
</script>
