import Vue from 'vue';
import sharedMutations from '../../shared/vue/store/mutations';

export default {
  ...sharedMutations,
  SET_DELETE_PENDING(state, payload) {
    state.deletePending = payload;
  },
  SET_DELETE_IN_PROGRESS(state, payload) {
    state.deleteInProgress = payload;
  },
  SET_DELETE_COMPLETED(state, payload) {
    state.deleteCompleted = payload;
  },
  SET_ASSET_TO_DELETE(state, payload) {
    state.assetToDelete = payload;
  },
  SET_EDIT_ASSET(state, payload) {
    state.editAsset = payload;
  },
  SET_EDIT_IMAGE(state, payload) {
    state.editImage = payload;
  },
  SET_ASSETS(state, payload) {
    state.assets = payload;
  },
  ADD_ASSETS(state, payload) {
    payload.forEach(asset => {
      state.assets.unshift(asset);
    });
  },
  SET_SIDEBAR(state, payload) {
    // Mutation may specify a "when" type criteria,
    // at which time we'll only make changes when in the checked type state.
    if (payload && payload.when && !payload.when.test(state.currentSidebar.type)) {
      return;
    }

    const type = payload ? payload.to : null;
    const sidebarComponents = [
      'asset-edit',
      'asset-upload'
    ];
    // make sure the payload.to value is a valid sidebar component name
    const valid = sidebarComponents.includes(type);

    state.currentSidebar = {
      type: valid ? type : null,
      title: valid ? (payload.title || null) : null,
      id: valid ? (payload.id || null) : null,
      childProps: valid ? (payload.childProps || null) : null
    };
  },
  SET_EDIT_INDEX(state, payload) {
    state.index = payload;
  },
  UPDATE_ASSETS(state, payload) {
    // assets and images use different fields so account for this
    if (payload.search_text) {
      payload.searchText = payload.search_text;
    }
    let temp = Object.assign({}, state.assets[state.index], payload);
    Vue.set(state.assets, state.index, temp);
  },
  SET_EDITING_LIST(state, payload) {
    state.editingList = payload;
  },
  SET_SHOW_METADATA(state, payload) {
    state.show_metadata = payload;
  },
  SET_HIGHLIGHT(state, payload) {
    state.showHighlight = payload;
  }
};
