<template>
  <div
    v-if="currentSidebarTag"
    id="modal"
    class="p-modal">

    <section class="p-modal__content">
      <div v-if="currentSidebar.title" class="m-field-group__subsection">
        <div class="title">{{ currentSidebar.title }}</div>
      </div>
      <component :is="currentSidebarTag" :id="currentSidebar.id" :childProps="currentSidebar.childProps" />
    </section>

    <res-button
      design="default"
      class="p-modal__close" >
      <res-icon name="x"/>
      <button
        type="button"
        @click="closeModal">Close</button>
    </res-button>

    <res-button v-if="currentSidebar.type === 'asset-upload' && show_metadata"
                design="default"
                class="p-modal__back">
      <res-icon name="left" />
      <button @click="goBack">Back</button>
    </res-button>
  </div>
</template>

<script>
import AssetEdit from './sidebars/asset-edit.vue';
import AssetUpload from './sidebars/asset-upload.vue';
import { mapState } from 'vuex';

export default {
  name: 'AppSidebar',
  components: {
    AssetEdit,
    AssetUpload
  },
  computed: {
    ...mapState([
      'currentSidebar',
      'show_metadata'
    ]),
    currentSidebarTag() {
      return this.currentSidebar.type || null;
    }
  },
  watch: {
    currentSidebarTag() {
      if (!this.currentSidebarTag) {
        this.$store.commit('SET_SIDEBAR', null);
      }
    }
  },
  methods: {
    closeModal() {
      if (this.currentSidebar.type === 'asset-edit') {
        this.$store.commit('SET_EDITING_LIST', []);
        setTimeout(() => {
          this.$store.dispatch('handleEditingStyle');
        }, 5000);
      }
      this.$store.commit('SET_SIDEBAR', null);
    },
    goBack() {
      this.$store.dispatch('goBack');
    }
  }
};
</script>
