import graphqlRequest from '../../client/lib/graphql_request';
import request from '../../client/lib/request';
import isPublished from '../../client/lib/is_published';
import messages from '../../client/config/messages';

/**
 * @param {File} file - https://developer.mozilla.org/en-US/docs/Web/API/File/File
 * @returns {Boolean} - true if the file should be uploaded to chorus-asset-service as an image
 */
const isImage = file => (
  file.type.includes('image') && file.type !== 'image/svg+xml'
);

const isFile = file => !isImage(file)

export default {
  async confirmDeleteAsset({ state, commit }) {
    let asset = state.assetToDelete;
    if (state.deletePending) {
      commit('SET_DELETE_PENDING', false);
      commit('SET_DELETE_IN_PROGRESS', true);
      try {
        if (asset.type !== 'IMAGE') {
          await graphqlRequest('/api/tower/graphql', {
            query: `
                  mutation RemoveAsset2($id: Int!) {
                    removeAsset(id: $id) {
                      id
                    }
                  }
                `,
            variables: JSON.stringify({ id: asset.id })
          });
          return;
        }

        // Delete asset and replace with placeholder image
        await request('/api/chorus/images/destroy_multiple', {
          method: 'DELETE',
          query: {
            community_id: state.currentCommunity.id
          },
          body: {
            chorus_asset_ids: [asset.id],
            user_id: state.currentUser.id,
            placeholder: true
          }
        });

        // Query for docs that contain the image
        let { docs } = await request(`/api/content/docs?chorus_asset_ids=${asset.id}`);
        // Filter the stories and subdocs, since we'll process them separately
        let storyDocs = docs.filter(doc => doc.schema_name === 'story');
        let subdocs = docs.filter(doc => doc.schema_name !== 'story');

        // If any of the returned docs is a subdoc, query for all docs containing that subdoc
        for (let subdoc of subdocs) {
          let result = await request(`/api/content/docs?schema_name=story&subdoc_ids=${subdoc.id}`);
          for (let doc of result.docs) {
            if (!storyDocs.find(storyDoc => storyDoc.id === doc.id)) {
              storyDocs.push(doc);
            }
          }
        }

        // Republish the published version of all published docs in the story schema to bust cache
        for (let doc of storyDocs) {
          if (isPublished(doc.status)) {
            await request(`/api/content/docs/${doc.id}/publish`, {
              method: 'POST',
              body: { republishPublished: true }
            });
          }
        }


      } catch (err) {
        commit('messageBanner/PUT_NOTICE', {
          type: 'negative',
          message: messages.UNEXPECTED
        });
        throw err;
      }
      commit('SET_DELETE_IN_PROGRESS', false);
      commit('SET_DELETE_COMPLETED', true);
    }
  },
  cancelDeleteAsset({ state, commit }) {
    if (state.deletePending) {
      commit('SET_DELETE_PENDING', false);
      commit('SET_ASSET_TO_DELETE', {});
    }
  },
  async handleEditing({ state, commit }, index) {
    let image = await request(`api/assets/images/${state.assets[index].id}`);
    commit('SET_EDIT_ASSET', state.assets[index]);
    commit('SET_EDIT_IMAGE', image);
    commit('SET_EDIT_INDEX', index);
    commit('SET_EDITING_LIST', [state.assets[index].id]);
  },
  async updateAsset({ state, dispatch, commit }, data) {
    await request(`/api/assets/images/${state.editAsset.id}`, {
      method: 'PUT',
      body: data,
      opts: { handleError: false }
    }).then((response) => {
      commit('UPDATE_ASSETS', response);
      commit('SET_EDITING_LIST', []);
      commit('messageBanner/PUT_NOTICE', {
        type: 'neutral',
        message: 'This asset has been updated.',
        timeout: true
      });
    }).catch((err) => {
      console.error('Updating asset failed: ', err);
      commit('messageBanner/PUT_NOTICE', {
        type: 'negative',
        message: messages.UNEXPECTED,
        timeout: true
      });
    });

    // close sidebar & remove styles after 5 seconds
    commit('SET_SIDEBAR', null);
    setTimeout(() => {
      dispatch('handleEditingStyle');
    }, 5000);
  },
  handleEditingStyle({ state, commit }) {
    if (state.editingList.length === 0) {
      commit('SET_EDIT_ASSET', {});
      commit('SET_EDIT_IMAGE', {});
    }
  },
  async uploadAssets({ state }, { files, body, remoteFileUrl }) {
    let uploadingImages = false;
    const formData = new FormData();
    if (remoteFileUrl) {
      formData.append('remote_file_url', remoteFileUrl);
      formData.append('source', 'url');
    } else {
      if (!(files.every(isImage) || files.every(isFile))) {
        const error = new Error('Only assets of the same file type can be uploaded in each batch.')
        error.name = 'ArgumentError';
        throw error;
      }

      files.forEach(file => {
        if (isImage(file)) {
          uploadingImages = true;
        }

        formData.append('files', file);
      });
      formData.append('source', `upload`);
    }

    // attach meta data, if set (or allow CAS to grab IPTC from image)
    formData.append('user_id', state.currentUser.id);
    formData.append('network_id', state.network.id);
    formData.append('community_id', state.currentCommunity.id);
    if (body.title.length) formData.append('title', body.title);
    if (body.credit.length) formData.append('credit', body.credit);
    if (body.search_text.length) formData.append('search_text', body.search_text);
    if (body.source_caption.length) formData.append('caption', body.source_caption);
    if (body.usage_rights.length) formData.append('usage_rights', body.usage_rights);

    if (remoteFileUrl) {
      return fetch(`/api/assets/images`, {
        method: `post`,
        body: formData
      });
    }
    const url = uploadingImages ? `api/assets/images/batch` : `api/assets/files/batch`;
    return fetch(url, {
      method: `post`,
      body: formData
    });
  },
  goBack({ commit }) {
    commit('SET_SHOW_METADATA', false);
  },
  addAssets({ commit }, data) {
    commit('SET_HIGHLIGHT', true);
    commit('ADD_ASSETS', data);
    setTimeout(() => {
      commit('SET_HIGHLIGHT', false);
    }, 5000);
  }
};
