<template>
  <res-modal
    id="confirmDeleteAssetResModal"
    ref="confirmDeleteAssetResModal"
    background="darken">
    <div>
      <p>Are you sure you want to delete this asset?</p>
      <p>This will permanently delete the file from the {{ networkName }} network and remove it from any related stories.</p>
      <p>This action can’t be undone. Please type “DELETE” to confirm.</p>
      <form @submit="confirmDeleteAsset()">
        <res-input class="m-b-1">
          <input
            v-model="text"
            type="text"
          />
        </res-input>
      </form>
      <res-button
        :disabled="text !== 'DELETE'"
        design="primary">
        <button
          type="button"
          class="is-primary"
          @click="confirmDeleteAsset()">Delete</button>
      </res-button>
      <res-button>
        <button
          type="button"
          class="is-secondary"
          @click="cancelDeleteAsset()">Cancel</button>
      </res-button>
    </div>
  </res-modal>
</template>

<script>
export default {
  props: {
    networkName: {
      type: String,
      required: true
    }
  },
  data() {
    return {
      text: ''
    };
  },
  methods: {
    confirmDeleteAsset() {
      this.resetAndClose();
      this.$store.commit('SET_DELETE_PENDING', true);
      this.$store.dispatch('confirmDeleteAsset');
    },
    cancelDeleteAsset() {
      this.resetAndClose();
      this.$store.dispatch('cancelDeleteAsset');
    },
    resetAndClose() {
      this.text = '';
      this.$refs.confirmDeleteAssetResModal.open = false;
    }
  }
};
</script>
